      <div class="picker-view-header">
        <div class="picker-view-lable picker-view-lable-cancel" @click="cancelEvent"></div>
        <div class="picker-view-lable picker-view-lable-commit" @click="commitEvent"></div>
      </div>
      <div class="picker-wrapper">
        <mt-picker :slots="addressSlots" @change="onValueChange" :visible-item-count="5"></mt-picker>
      </div>
    </div>
