<style lang="scss">
  $baseFontSize: 750px;
  $defaultColor: #191919;
  @function pxToRem($px) {
    @return $px / $baseFontSize * 10rem;
  }
  $defaultSize: pxToRem(24px);

  #picker {
    background: rgba(25,25,25,0.30);
    position: fixed;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    z-index: 666;
  }
  #picker-view {
    position: absolute;
    height: pxToRem(470px);
    background-color: #EEEEEE;
    bottom: 0px;
    left: 0px;
    right: 0px;
  }
  #picker-wrapper {
    bottom: 0px;
    top: pxToRem(100px);
    background-color: #EEEEEE;
  }
  .picker-view-header {
    height: pxToRem(100px);
    background-color: white;
    left: 0;
    right: 0;
    top: 0;

  }
  .picker-center-highlight:before, .picker-center-highlight:after {
    background-color: #BDBDBD;
  }
  .picker-items {
    background-color: #EEEEEE;
  }
  .picker-view-lable {
    color: #191919;
    font-size: pxToRem(24px);
    height: pxToRem(100px);
    line-height: pxToRem(100px);
    display: inline-block;
  }
  .picker-view-lable-commit {
    margin-left: pxToRem(576px);
  }
  .picker-view-lable-cancel {
    margin-left: pxToRem(40px);
  }


</style>


